'use client';

import { useEffect, useRef, useState } from 'react';
import jwtDecode from 'jwt-decode';
import { Send, ArrowLeft, User, Bot } from 'lucide-react';
import socketService from '@/services/socketService';
import chatService from '@/services/chatService';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';

interface Message {
  id: number;
  sender: 'user' | 'other';
  text: string;
  timestamp?: string;
}

interface DecodedToken {
  id: string;
  email: string;
  name: string;
}

const ChatPage = () => {
  const [userName, setUserName] = useState('');
  const [userEmail, setUserEmail] = useState('');
  const [chatId, setChatId] = useState('');
  const [newMessage, setNewMessage] = useState('');
  const [messages, setMessages] = useState<Message[]>([]);
  const [typingStatus, setTypingStatus] = useState('');
  const typingTimeoutRef = useRef<NodeJS.Timeout | null>(null);
  const lastTypingTimeRef = useRef<number>(0);

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (token) {
      const decoded: DecodedToken = jwtDecode(token);
      setUserEmail(decoded.email);
      setUserName(decoded.name);
    }
  }, []);

  useEffect(() => {
    if (userEmail) {
      chatService
        .createOrGetChat(userEmail)
        .then((data) => {
          setChatId(data.chatId);
        })
        .catch((error) => console.error('Error creating/getting chat:', error));
    }
  }, [userEmail]);

  useEffect(() => {
    if (chatId) {
      socketService.connect();

      socketService.joinChat(chatId);

      socketService.onMessage((message: Message) => {
        setMessages((prev) => [...prev, message]);
      });

      socketService.onTyping((typingUser) => {
        setTypingStatus(`${typingUser} is typing...`);
        if (typingTimeoutRef.current) clearTimeout(typingTimeoutRef.current);
        typingTimeoutRef.current = setTimeout(() => setTypingStatus(''), 2000);
      });

      socketService.onStopTyping(() => {
        setTypingStatus('');
      });

      return () => {
        socketService.disconnect();
      };
    }
  }, [chatId]);

  const handleSendMessage = () => {
    if (!newMessage.trim() || !chatId) return;

    socketService.sendMessage({
      chatId,
      text: newMessage,
      sender: 'user',
    });

    setMessages((prev) => [
      ...prev,
      {
        id: Date.now(),
        sender: 'user',
        text: newMessage,
        timestamp: new Date().toISOString(),
      },
    ]);

    setNewMessage('');
    socketService.stopTyping(chatId, 'user');
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setNewMessage(value);

    const now = Date.now();
    const shouldSend = now - lastTypingTimeRef.current > 500;

    if (chatId && value.trim()) {
      if (shouldSend) {
        socketService.startTyping(chatId, 'user');
        lastTypingTimeRef.current = now;
      }

      if (typingTimeoutRef.current) clearTimeout(typingTimeoutRef.current);
      typingTimeoutRef.current = setTimeout(() => {
        socketService.stopTyping(chatId, 'user');
      }, 3000);
    } else if (chatId) {
      socketService.stopTyping(chatId, 'user');
    }
  };

  if (!userName || !userEmail) {
    return (
      <div className="flex justify-center items-center h-screen bg-black text-white">
        <p>Loading chat...</p>
      </div>
    );
  }

  return (
    <div className="p-4 min-h-screen bg-black text-white flex flex-col">
      <div className="flex items-center space-x-2 mb-4">
        <ArrowLeft />
        <h2 className="text-xl font-semibold">Chat with Arleen</h2>
      </div>

      <div className="flex-1 space-y-2 overflow-y-auto mb-4">
        {messages.map((message, index) => (
          <Card
            key={index}
            className={`max-w-xs ${
              message.sender === 'user' ? 'ml-auto bg-gray-800' : 'mr-auto bg-white text-black'
            }`}
          >
            <CardContent className="p-2">
              <div className="flex items-center mb-1 text-sm text-gray-400">
                {message.sender === 'user' ? <User className="w-4 h-4 mr-1" /> : <Bot className="w-4 h-4 mr-1" />}
                <span>{message.sender === 'user' ? userName : 'Arleen'}</span>
              </div>
              <p>{message.text}</p>
              {message.timestamp && (
                <div className="text-xs text-gray-500 mt-1">
                  {new Date(message.timestamp).toLocaleTimeString()}
                </div>
              )}
            </CardContent>
          </Card>
        ))}
      </div>

      {typingStatus && (
        <div className="mb-2 text-gray-400 italic text-sm">{typingStatus}</div>
      )}

      <div className="flex items-center space-x-2 mt-auto">
        <Input
          value={newMessage}
          onChange={handleInputChange}
          placeholder="Type a message..."
          className="flex-1"
        />
        <Button onClick={handleSendMessage}>
          <Send className="w-4 h-4" />
        </Button>
      </div>
    </div>
  );
};

export default ChatPage;
